<template>
  <el-card class="box-card">
    <el-row>
      <div class="head">当前借阅</div>
    </el-row>
    <p></p>
    <el-row>
      图书名称：<el-col :span="5">
      <el-input v-model="this.searchBookName" placeholder="请输入图书名称"></el-input>
    </el-col>&nbsp;
      借阅者：<el-col :span="5">
      <el-input v-model="this.searchBorrowerName" placeholder="请输入借阅者姓名"></el-input>
    </el-col>&nbsp;
      <el-col :span="2">
        <el-button @click="search">
          <el-icon>
            <Search/>
          </el-icon>
        </el-button>
      </el-col>
    </el-row>
    <el-table :data="this.currentBorrow" border stripe>
      <el-table-column type="index" label="序号" align="center"></el-table-column>
      <el-table-column label="图书名称" prop="bookName" align="center"></el-table-column>
      <el-table-column label="图书作者" prop="bookAuthor" align="center"></el-table-column>
      <el-table-column label="书籍状态"  align="center">
        <template v-slot="scope">
          <el-tag v-if="scope.row.bookStatus==1">借阅中</el-tag>
          <el-tag v-if="scope.row.bookStatus==0">可借阅</el-tag>
        </template>
      </el-table-column>
      <el-table-column label="借阅人" prop="bookBorrower" align="center"></el-table-column>
      <el-table-column label="借阅时间" prop="bookBorrowtime" align="center"></el-table-column>
      <el-table-column label="预计归还时间" prop="bookReturntime" align="center"></el-table-column>
      <el-table-column label="操作" align="center">
        <template v-slot="scope">
          <el-button type="success" v-if="scope.row.bookStatus==1" @click="getBook(scope.row.bookId)">
            还书
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog v-model="dialogFormVisible" title="确定归还" style="text-align: center">
      <el-descriptions
          :column="4"
          :size="size"
          border
      >
        <el-descriptions-item label="图书名称">
          <el-tag>{{this.book.bookName}}</el-tag>
        </el-descriptions-item>
        <el-descriptions-item label="标准ISBN">
          <el-tag>{{this.book.bookIsbn}}</el-tag>
        </el-descriptions-item>
        <el-descriptions-item label="出版社" :span="2">
          <el-tag>{{this.book.bookPress}}</el-tag>
        </el-descriptions-item>
        <el-descriptions-item label="作者">
          <el-tag>{{this.book.bookAuthor}}</el-tag>
        </el-descriptions-item>
        <el-descriptions-item label="书籍页数">
          <el-tag>{{this.book.bookPagination}}</el-tag>
        </el-descriptions-item>
        <el-descriptions-item label="借阅人">
          <el-tag>{{this.book.bookBorrower}}</el-tag>
        </el-descriptions-item>
      </el-descriptions>
      <p></p>
      <el-button type="info" round @click="this.dialogFormVisible=false">取消</el-button>
      <el-button type="success" round @click="returnBook(this.book)">确定</el-button>
    </el-dialog>
  </el-card>
</template>

<script>
export default {
  name: "currentBorrow",
  data(){
    return{
      searchBookName:'',
      searchBorrowerName:'',
      currentBorrow:[],
      dialogFormVisible:false,
      book: {
        bookId:'',
        bookName:'',
        bookIsbn:'',
        bookPress:'',
        bookAuthor:'',
        bookPagination:'',
        bookReturntime:'',
        bookPrice:'',
        bookUploadtime:'',
        bookStatus:'',
        bookBorrower:'',
        bookBorrowtime:'',
      }
    }
  },
  created() {
    this.$http({
      url: 'http://localhost:8080/book/borrowSelect',
      method: 'post',
      data:{
        "bookName":this.searchBookName,
        "bookBorrower":this.searchBorrowerName
      }
    }).then(res => {
      this.currentBorrow = res.data.data;
    })
  },
  methods:{
    search(){
      this.$http({
        url: 'http://localhost:8080/book/borrowSelect',
        method: 'post',
        data:{
          "bookName":this.searchBookName,
          "bookBorrower":this.searchBorrowerName
        }
      }).then(res => {
        this.currentBorrow = res.data.data;
      })
    },
    getBook(id){
      this.dialogFormVisible=true;
      this.$http({
        url: 'http://localhost:8080/book/'+id,
        method: 'get',
      }).then(res=>{
        this.book=res.data.data;
      })
    },
    returnBook(book){
      this.dialogFormVisible=false;
      this.$http({
        url:'http://localhost:8080/book/returnBook',
        method:'post',
        data: {
          "bookId":book.bookId,
          "bookName":book.bookName,
          "bookIsbn":book.bookIsbn,
          "bookPress":book.bookPress,
          "bookAuthor":book.bookAuthor,
          "bookPagination":book.bookPagination,
          "bookPrice":book.bookPrice,
          "bookUploadtime":book.bookUploadtime,
          "bookStatus":book.bookStatus,
          "bookBorrower":book.bookBorrower,
          "bookBorrowtime":book.bookBorrowtime,
          "bookReturntime":book.bookReturntime
        }
      }).then(res=>{
        if(res.data.code==1){
          this.$message.success(res.data.data);
          this.$http({
            url: 'http://localhost:8080/book/borrowSelect',
            method: 'post',
            data:{
              "bookName":this.searchBookName,
              "bookBorrower":this.searchBorrowerName
            }
          }).then(res => {
            this.currentBorrow = res.data.data;
          })
        }else{
          this.$message("借阅失败");
        }
      })
    }
  }
}
</script>

